input:not([type='file']), textarea {
  border: 0;
  border-bottom: 1px solid #eee;
  outline:none;
  &.input {
    &--error {
      border-color: red;
    }
  }
}
label{
  color: $blue;
}

.button{
  background-color: $blue;
  outline: none;
  border: none;
}



