@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,700");
body {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 12px; }

html {
  position: relative;
  min-height: 100%; }

body {
  margin: 0 0 70px; }

.inner {
  max-width: 1400px;
  width: 90vw;
  margin: 0 auto; }

.pagination__prev, .pagination__text, .pagination__next {
  width: 33.3333333%; }

.store__card {
  width: 33.33333%;
  height: 300px;
  overflow: hidden; }
  .store__card img {
    transition: all 1s; }
  .store__card:hover img {
    transform: scale(1.1); }

.store__card--description {
  background: rgba(0, 0, 0, 0.8);
  height: 100px; }

.store__card--title a {
  background-color: #2D51A3; }

.store__hero {
  height: 60vh; }
  .store__hero .store__hero--overlay {
    background-color: rgba(0, 0, 0, 0.5); }
  .store__hero img {
    object-fit: cover; }
  .store__hero .store__title {
    margin-top: 13%;
    background-color: #2D51A3; }

@media only screen and (max-width: 1200px) {
  .store__card {
    width: 50%; } }

@media only screen and (max-width: 750px) {
  .store__card {
    width: 90%;
    margin: 0 auto; } }

input:not([type='file']), textarea {
  border: 0;
  border-bottom: 1px solid #eee;
  outline: none; }
  input:not([type='file']).input--error, textarea.input--error {
    border-color: red; }

label {
  color: #2D51A3; }

.button {
  background-color: #2D51A3;
  outline: none;
  border: none; }

.search {
  position: relative; }
  .search__results {
    background: white;
    position: absolute;
    width: 100%;
    z-index: 4; }
  .search__input {
    background: url("/images/search.svg") no-repeat scroll 1px;
    padding-left: 35px !important; }
  .search__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec; }

.map {
  padding: 20px;
  background: white; }

#map {
  height: 500px; }

.popup {
  width: 300px; }
  .popup img {
    width: 100%; }

.heart__button {
  background: none;
  border: 0;
  outline: 0;
  position: relative; }
  .heart__button:after {
    content: '♥️';
    font-size: 20px;
    position: absolute;
    opacity: 0;
    top: 0; }
  .heart__button svg {
    width: 25px;
    fill: white; }
  .heart__button--hearted svg {
    fill: red; }
  .heart__button--float:after {
    animation: fly 2.5s 1 ease-out; }

@keyframes fly {
  0% {
    transform: translateY(0);
    left: 0;
    opacity: 1; }
  20% {
    left: 20px; }
  40% {
    left: -20px; }
  60% {
    left: 20px; }
  80% {
    left: -20px; }
  100% {
    transform: translateY(-400px);
    opacity: 0;
    left: 20px; } }

.review {
  background: white;
  border: 1px solid grey;
  border-bottom: 0;
  border-bottom: 1px solid grey;
  margin-bottom: 2rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
  padding-left: 5px; }
  .review:before {
    width: 5px;
    left: 0;
    content: '';
    display: block;
    background: #303030;
    position: absolute;
    height: 100%;
    background-attachment: fixed; }
  .review__header {
    border-bottom: 1px solid grey;
    display: flex; }
    .review__header > * {
      border-right: 1px solid grey;
      padding: 0.2rem;
      display: flex;
      align-items: center;
      flex: 1;
      justify-content: center; }
      .review__header > *:last-child {
        border-right: 0; }
  .review__author {
    justify-content: flex-start;
    padding-left: 2rem;
    display: flex; }
    .review__author .avatar {
      margin-right: 2rem; }
  .review__time {
    font-size: 1.2rem;
    color: #999999; }
  .review__stars {
    color: #FFC40E; }
  .review__body {
    padding: 2rem; }
  .review p {
    white-space: pre-wrap; }

.reviewer {
  position: relative;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem; }
  .reviewer__stars {
    display: flex;
    justify-content: center; }
    .reviewer__stars input {
      display: none; }
      .reviewer__stars input:checked ~ label {
        color: #FFC40E; }
      .reviewer__stars input + label {
        font-size: 0;
        /* These are in the opposite DOM order
           re-order them to be visually in normal order
           This is fine for accessibility because our labels have for()
         */ }
        .reviewer__stars input + label:before {
          content: '★';
          font-size: 2rem; }
        .reviewer__stars input + label[for="star5"] {
          order: 5; }
        .reviewer__stars input + label[for="star4"] {
          order: 4; }
        .reviewer__stars input + label[for="star3"] {
          order: 3; }
        .reviewer__stars input + label[for="star2"] {
          order: 2; }
        .reviewer__stars input + label[for="star1"] {
          order: 1; }
        .reviewer__stars input + label:hover, .reviewer__stars input + label:hover ~ label {
          color: #ffdd74; }
  .reviewer textarea {
    border: 0;
    outline: 0;
    font-size: 2rem;
    padding: 2rem;
    height: 200px; }
  .reviewer__meta {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid grey; }
    .reviewer__meta > * {
      flex: 1; }

.icon svg {
  width: 25px;
  height: 25px; }

.logo {
  height: 60px; }

.menu_item {
  padding-top: 20px; }

.menu_item:hover .menu_text, .menu_item.active .menu_text {
  border-bottom: 2px solid #2D51A3; }

.menu_item:hover svg path, .menu_item.active svg path {
  fill: #2D51A3; }

.side__menu {
  display: none;
  width: calc(266.7px + 5vw);
  height: 100vh;
  background: #2D51A3;
  position: absolute;
  z-index: 5;
  right: 0; }

.flash-messages {
  width: calc(266.7px + 5vw);
  float: right; }

.flash--success, .flash--error {
  border: 5px solid #2D51A3;
  border-right: none;
  border-radius: 10px 0 0 10px; }

.flash--close {
  background: none;
  border: none;
  outline: none; }

@media only screen and (min-width: 1400px) {
  .side__menu, .flash-messages {
    width: calc((100vw - 1400px) / 2 + 266.7px); } }



