.search {
  position: relative;
  &__results {
    background: white;
    position: absolute;
    width: 100%;
    z-index: 4;
  }
  &__input {
    background: url('/images/search.svg') no-repeat scroll 1px;
    padding-left: 35px !important;
  }
  &__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec;
  }
}



